import {Navbar,Nav,Modal} from "react-bootstrap";
import {useState,useEffect} from "react";
import GoogleLogin from 'react-google-login';
import AddNote from "./Form";
import "./Navbar.css";
import {store} from "../store/store";
import {useDispatch} from "react-redux";


//MODAL TO ALLOW NEW NOTE SUBMISSION -> BOOTSTRAP MODAL
function FormModal(props) {
    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            Modal heading
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
        {/*AddNote -> form to allow new submission */}
         <AddNote/>
        </Modal.Body>
      </Modal>
    );
  }
  
//NAVBAR COMPONENT EXPORTED
export default function Bar(){
    
  const [modalShow, setModalShow] =useState(false);//FOR MODAL VISIBILITY

  
  const dispatch = useDispatch();
  
  const [eff,setEff]=useState(0);
  
  const [user,setUser] = useState(JSON.parse(localStorage.getItem('profile')));//TO SET USER
  
  store.subscribe(()=>{
        setEff(store.getState());
    });//TO DETECT CHANGES IN STORE SO THAT NEW DATA CAN BE SHOWN , RE-RENDERED
  
  useEffect(()=>{
        setModalShow(false);
  },[eff]);

  const loginSuccess = (res)=>{
    const result = res?.profileObj;
    const token = res?.tokenId;
   
    try {
      dispatch({type:"AUTH",data:{result,token}});
      } catch (error) {
      console.log(error);
      }
      setUser(JSON.parse(localStorage.getItem('profile')));
    }
    const loginFail = (err)=>{
      console.log(err);
    }
    const logout = () =>{
      dispatch({type:"LOGOUT"});
      setUser(null);
    }
    return( 
    <>
        <Navbar  variant="dark" className="nav-body ">
        <div className="container">
          <Navbar.Brand href="#home"> <span className="nav-txt" >Keep A Note</span></Navbar.Brand>
                <Nav className="ml-auto ">
                    {/* <Nav.Link href="#home"> <span className="nav-txt bttn">LOGIN</span> </Nav.Link> */}
                    {/* <Nav.Link href="#pricing"> <span className="nav-txt">ADD</span> </Nav.Link> */}

                    {/* LOGIN WITH GOOGLE */}
                    {user?
                      <button className="login-text btn-g" onClick={logout}><span className="out-icon"> <i class="fas fa-sign-out-alt"></i><img src={user.result.imageUrl} alt={user.result.name} width="18px" height="18px" style={{borderRadius:"50%"}}/> </span> <span className="out-text">LOGOUT <img src={user.result.imageUrl} alt={user.result.name} width="22px" height="22px" style={{borderRadius:"50%"}}/></span> </button>
                      :
                      <GoogleLogin
                        
                        clientId={process.env.REACT_APP_GOOGLE_CLIENTID}
                        render={renderProps => (
                        <button className="btn-g" onClick={renderProps.onClick} id="btn-g-1" disabled={renderProps.disabled}><span className="login-text login-with login-text-2">LOGIN WITH </span><i className="fab fa-google"></i></button>
                        )}
                        buttonText="Login"
                        onSuccess={loginSuccess}
                        onFailure={loginFail}
                        cookiePolicy={'single_host_origin'}
                      />
                      
                    }
                    <button className={`login-text ${user?"btn-g":"btn-dis"}`} disabled={user?false:true}  onClick={() => setModalShow(true)}><span className="add-icon"><i class="fas fa-plus"></i></span><span className="add-text">ADD</span></button>
                </Nav>
        </div>
        </Navbar>
        <FormModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        />
    </>
    )
}
